"use client";

import { useState, useEffect, useRef } from 'react';

export interface UseTimerReturn {
  time: number;
  isActive: boolean;
  startTimer: (customStartTime?: Date) => void;
  stopTimer: () => { duration: number; actualStartTime: number };
  resetTimer: () => void;
  formatTime: (milliseconds: number) => string;
  getActualStartTime: () => number;
}

export function useTimer(): UseTimerReturn {
  const [time, setTime] = useState<number>(0);
  const [isActive, setIsActive] = useState<boolean>(false);
  const intervalRef = useRef<NodeJS.Timeout | null>(null);
  const startTimeRef = useRef<number>(0);
  const actualStartTimeRef = useRef<number>(0);

  useEffect(() => {
    if (isActive) {
      intervalRef.current = setInterval(() => {
        setTime(Date.now() - startTimeRef.current);
      }, 100);
    } else {
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
        intervalRef.current = null;
      }
    }

    return () => {
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
      }
    };
  }, [isActive]);

  const startTimer = (customStartTime?: Date) => {
    if (!isActive) {
      const now = Date.now();
      if (customStartTime) {
        actualStartTimeRef.current = customStartTime.getTime();
        startTimeRef.current = now - (now - customStartTime.getTime());
        setTime(now - customStartTime.getTime());
      } else {
        actualStartTimeRef.current = now;
        startTimeRef.current = now;
        setTime(0);
      }
      setIsActive(true);
    }
  };

  const stopTimer = (): { duration: number; actualStartTime: number } => {
    if (isActive) {
      setIsActive(false);
      const finalTime = time;
      return {
        duration: finalTime,
        actualStartTime: actualStartTimeRef.current
      };
    }
    return {
      duration: time,
      actualStartTime: actualStartTimeRef.current
    };
  };

  const resetTimer = () => {
    setIsActive(false);
    setTime(0);
    startTimeRef.current = 0;
    actualStartTimeRef.current = 0;
  };

  const getActualStartTime = (): number => {
    return actualStartTimeRef.current;
  };

  const formatTime = (milliseconds: number): string => {
    const totalSeconds = Math.floor(milliseconds / 1000);
    const hours = Math.floor(totalSeconds / 3600);
    const minutes = Math.floor((totalSeconds % 3600) / 60);
    const seconds = totalSeconds % 60;

    return `${hours.toString().padStart(2, '0')}:${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`;
  };

  return {
    time,
    isActive,
    startTimer,
    stopTimer,
    resetTimer,
    formatTime,
    getActualStartTime
  };
}
