export interface Session {
  id: string;
  startTime: number;
  duration: number;
  endTime: number;
}

const STORAGE_KEY = 'keuschheitsTrackerSessions';

export class StorageError extends Error {
  constructor(message: string, public cause?: Error) {
    super(message);
    this.name = 'StorageError';
  }
}

export function getSessions(): Session[] {
  try {
    if (typeof window === 'undefined') {
      return [];
    }

    const stored = localStorage.getItem(STORAGE_KEY);
    if (!stored) {
      return [];
    }

    const sessions = JSON.parse(stored);
    return Array.isArray(sessions) ? sessions : [];
  } catch (error) {
    console.error('Fehler beim Laden der Sitzungen:', error);
    throw new StorageError('Sitzungen konnten nicht geladen werden', error as Error);
  }
}

export function saveSession(session: Session): void {
  try {
    if (typeof window === 'undefined') {
      throw new StorageError('Local Storage ist nicht verfügbar');
    }

    const existingSessions = getSessions();
    const updatedSessions = [...existingSessions, session];
    
    localStorage.setItem(STORAGE_KEY, JSON.stringify(updatedSessions));
  } catch (error) {
    console.error('Fehler beim Speichern der Sitzung:', error);
    if (error instanceof StorageError) {
      throw error;
    }
    throw new StorageError('Sitzung konnte nicht gespeichert werden', error as Error);
  }
}

export function clearSessions(): void {
  try {
    if (typeof window === 'undefined') {
      throw new StorageError('Local Storage ist nicht verfügbar');
    }

    localStorage.removeItem(STORAGE_KEY);
  } catch (error) {
    console.error('Fehler beim Löschen der Sitzungen:', error);
    throw new StorageError('Sitzungen konnten nicht gelöscht werden', error as Error);
  }
}

export function getTotalDuration(): number {
  try {
    const sessions = getSessions();
    return sessions.reduce((total, session) => total + session.duration, 0);
  } catch (error) {
    console.error('Fehler beim Berechnen der Gesamtdauer:', error);
    return 0;
  }
}

export function formatDuration(milliseconds: number): string {
  const totalSeconds = Math.floor(milliseconds / 1000);
  const hours = Math.floor(totalSeconds / 3600);
  const minutes = Math.floor((totalSeconds % 3600) / 60);
  const seconds = totalSeconds % 60;

  if (hours > 0) {
    return `${hours}h ${minutes}m ${seconds}s`;
  } else if (minutes > 0) {
    return `${minutes}m ${seconds}s`;
  } else {
    return `${seconds}s`;
  }
}

export function formatDate(timestamp: number): string {
  const date = new Date(timestamp);
  return date.toLocaleDateString('de-DE', {
    day: '2-digit',
    month: '2-digit',
    year: 'numeric',
    hour: '2-digit',
    minute: '2-digit'
  });
}
